.distance {
  display: flex;
}
.pageButton {
  max-height: 0px;
  max-width: 0px;
}
button:last-of-type {
  height: 50px;
}
div {
  margin: 0px;
  padding: 0px;
}
ul {
  margin: 0px;
  padding: 0px;
}
.miniContainer {
  display: flex;
  flex-wrap: wrap;
  margin: 25px 50px;
}
.box {
  display: grid;
  grid-template-columns: repeat(8, 1fr);
  grid-template-rows: repeat(8, 5vw);
  grid-gap: 1.5rem;
  margin: 0px;
  padding: 0px;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  margin-right: 40px;
  margin-bottom: 20px;
  min-width: 320px;
  min-height: 200px;
  border: 2px dashed rgb(29, 69, 130);
}
.box:nth-child(3n) {
  margin-right: 0px;
}
.list {
  display: flex;
  flex-direction: row;
  width: 100%;
  height: 100%;
}
.list .el {
  display: flex;
  flex-direction: row;
  justify-content: center;
  width: inherit;
  height: inherit;
}

img {
  display: flex;
  display: block;
  width: 270px;
  height: 215px;
}
